.main-footer {
  background: hsl(0, 0%, 95%);
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
}

.main-footer__wrapper {
  max-width: 1050px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.main-footer__copyright {
  display: flex;
  flex-wrap: wrap;
  min-width: 200px;
  justify-content: space-between;
  align-items: center;
  min-height: 80px;
}

.main-footer__divider {
  width: 100%;
  border-bottom: solid 1px hsl(0, 0%, 90%);
}

.main-footer__logo {
  display: none;
}

@media (min-width: $tablet-width) {
  .main-footer__logo {
    display: block;
    min-width: 120px;
  }

  .main-footer__wrapper {
    justify-content: space-between;
    padding: 50px;
    padding-top: 27px;
    padding-bottom: 20px;
  }

  .main-footer__divider {
    max-width: 150px;
    border: none;
  }
}

@media (min-width: $desktop-width) {
  .main-footer__wrapper {
    padding-top: 27px;
    padding-bottom: 20px;
  }
}
